<script>
var apiUrl = "http://172.16.16.199:8081/eepd/statistics/";
export default {
  name: "demo",
  data() {
    return {
      colorCard: []
    };
  },
  mounted() {
    this.getAll();
  },

  methods: {

     //系统申报情况一览
    getAll() {
    
    
      // axios({
      //   method: "get",
      //   url: "" + apiUrl + "System"
      // })
      //   .then(function(resp) {
      //     console.log(resp.data);
      //     that.getSystemBar(resp.data);
      //   })
      //   .catch(resp => {
      //     console.log("请求失败：" + resp.status + "," + resp.statusText);
      //   });
        var that = this;
      //三单申报 
     var request1 = "" + apiUrl + "Events_Threebill";
     //遣返人员申报 
     var request2 = "" + apiUrl + "Events_Repatriation";
     // 枪支弹药申报 
     var request3 = "" + apiUrl + "Events_Guns"; 
     //旅游团申报 
    var request4= "" + apiUrl + "Events_Touristgroup"; 
    //特殊航班API申报
     var request5 = "" + apiUrl + "Events_Airport"; 
     // 24小时免签入境申报 
      var request6 = "" + apiUrl + "Events_Visa"; 
     
     function getEvents_Threebill() {
          return axios({
              methods:"get",
              url: request1, 
          });
      }
       function getEvents_Repatriation() {
          return axios({
              methods:"get",
              url: request2, 
          });
      }
      function getEvents_Guns() {
          return axios({
              methods:"get",
              url: request3, 
          });
      }
      function getEvents_Touristgroup() {
          return axios({
              methods:"get",
              url: request4, 
          });
      }
       function getEvents_Airport() {
          return axios({
              methods:"get",
              url: request5, 
          });
      }
      function getEvents_Visa() {
          return axios({
              methods:"get",
              url: request6, 
          });
      }
         axios.all([getEvents_Threebill(), getEvents_Repatriation(),getEvents_Guns(),
         getEvents_Touristgroup(),getEvents_Airport(),getEvents_Visa()])
      .then(
            axios.spread((threebill, repatriation, guns,touristgroup,airport,visa) => {
             
              var res1 = that.getNewList(threebill.data.result);
              var res2 = that.getNewList(repatriation.data.result);
              var res3 = that.getNewList(guns.data.result);
              var res4 = that.getNewList(touristgroup.data.result);
              var res5 = that.getNewList(airport.data.result);
              var res6 = that.getNewList(visa.data.result);
 
              // var colorCard= [
              //   { color: "#3499FF", text: "“三单”申报",data:res1.dateList,showData:res1.param},
              //   { color: "#3CD6B1", text: "遭返人员申报",data:res2.dateList,showData:res2.param },
              //   { color: "#FF7676", text: "枪支弹药申报",data:res3.dateList,showData:res3.param},
              //   { color: "#FFBF5F", text: "旅游团申报",data:res4.dateList,showData:res4.param},
              //   { color: "#FACC14", text: "特殊航班API申报",data:res5.dateList,showData:res5.param },
              //   { color: "#32B96B", text: "24小时免签入境申报",data:res6.dateList,showData:res6.param}
              // ]

               var colorCard= [
                { color: "#3499FF", text: "“三单”申报",data:res1},
                { color: "#3CD6B1", text: "遭返人员申报",data:res2 },
                { color: "#FF7676", text: "枪支弹药申报",data:res3},
                { color: "#FFBF5F", text: "旅游团申报",data:res4},
                { color: "#FACC14", text: "特殊航班API申报",data:res5 },
                { color: "#32B96B", text: "24小时免签入境申报",data:res6}
              ]
            
           
            that.getbar(colorCard)

            
             console.log('全部加载完成')
    
            })
          )
          .catch(err => {
            console.log(err.response)
          });

    },
    getNewList(list){
      
      var newParm={};
      
     newParm.dateList = [];
      newParm.param =[]
      list.map((item, idx) => {
        newParm.dateList.push(item[0]);
        newParm.param.push(item[1]);
       
      });
      
      return newParm

    },
    //为图表计算高度
    chartssize(container, charts) {
      function getStyle(el, name) {
        if (window.getComputedStyle) {
          return window.getComputedStyle(el, null);
        } else {
          return el.currentStyle;
        }
      }
      var wi = getStyle(container, "width").width;
      var hi = getStyle(container, "height").height;
      charts.style.height = hi;
      //  charts.style.width = wi
    },
   
    startChart: function(chartPid, chartId, option) {
      let myChartId = document.getElementById(chartId);
      //高度计算

      this.chartssize(document.getElementById(chartPid), myChartId);

      let myChart = echarts.init(myChartId);

      // 为echarts对象加载数据
      myChart.clear();
      myChart.setOption(option, true);

      window.addEventListener("resize", function() {
        myChart.resize();
      });
    },

    getbar: function(colorCard) {
     
     
    colorCard.forEach((item, idx) => {
       console.log('item',item)
      

        var option = {
          title: {
            text: item.text,
              textStyle:{
                //文字颜色
                color:'#475669',
                //字体风格,'normal','italic','oblique'
                fontStyle:'normal',
                //字体粗细 'normal','bold','bolder','lighter',100 | 200 | 300 | 400...
               
                //字体系列
                fontFamily:'PingFangSC-Medium',
                //字体大小
        　　　　 fontSize:18
        
            }
          },
         

          color: item.color,
          tooltip: {
            trigger: "axis",
            axisPointer: {
              // 坐标轴指示器，坐标轴触发有效
              type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          grid: {
            left: "3%",
            right: "4%",
            bottom: "3%",
            containLabel: true
          },
          xAxis: [
            {
              type: "category",
              data: item.data.dateList,
              axisTick: {
                alignWithLabel: true
              }
            }
          ],
          yAxis: [
            {
              type: "value"
            }
          ],
          series: [
            {
              name: "次数",
              type: "bar",
              barWidth: "60%",
              data:item.data.param
            }
          ]
        };

        this.startChart("pt", "baritem" + (idx + 1) + "", option);
      });
    }
  }
};
</script>
