<template>
  <div class="weather">
    <div class="ct">
      <div class="item">
        <div class="flex_long">
          <div class="title">系统申报情况一览</div>
          <div class="long">
            <div class="card" :style="'color:'+item.color" v-for="(item) in colorCard" :key="item">
              <p>{{item.current}}</p>
              <el-progress
                :color="item.color"
                :show-text="false"
                type="circle"
                :stroke-width="10"
                :percentage="100"
              ></el-progress>
              <i id="icon" :class="item.icon"></i>
              <p>{{item.num}}次</p>
            </div>
          </div>
        </div>
        <div class="flex_sort">
          <div class="title">旅行团人员申报情况</div>
          <div class="sort" id="parent">
            <div id="chartmain" :style="{width: '400px', height: '300px'}"></div>
          </div>
        </div>
      </div>

      <div class="item">
        <div class="flex_long">
          <div class="title">事项申报情况</div>
          <div class="long" id="parent">
            <div id="infoLine" :style="{width: '800px', height: '300px'}"></div>
          </div>
        </div>
        <div class="flex_sort">
          <div class="title">旅行社申报情况</div>
          <div class="sort" id="parent">
            <div id="infoBar" :style="{width: '400px', height: '300px'}"></div>
          </div>
        </div>
      </div>

      <div class="item">
        <div class="flex_long">
          <div class="title">个人申报情况</div>
          <div class="long" id="parent">
            <div id="per_declaration" :style="{width: '800px', height: '300px'}"></div>
          </div>
        </div>
        <div class="flex_sort">
          <div class="title">航空公司申报情况</div>
          <div class="sort" id="parent">
            <div id="pieOption" :style="{width: '400px', height: '300px'}"></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
var apiUrl = "http://172.16.16.199:8081/eepd/statistics/";
export default {
  name: "weather",
  data() {
    return {
      colorCard: []
    };
  },
  mounted() {
    this.getData()
    window.setInterval(() => {
      var that = this;
      setTimeout( that.getData(), 0)
    }, 1200000)
   
   
  },

  methods: {
    getData(){
       //系统申报情况一览
        this.getSystem();
        //旅行团人员申报情况
        this.getTravelgroup();
        //事项申报情况
        this.getEvents();
        //旅行社申报情况
        this.getTravelagency();
        //个人申报情况
        this.getPersonal()
        //航空公司申报情况
        this.getAirport();

    },
    //系统申报情况一览
    getSystem() {
      var that = this;
      axios({
        method: "get",
        url: "" + apiUrl + "System"
      })
        .then(function(resp) {
          console.log(resp.data);
          that.getSystemBar(resp.data);
        })
        .catch(resp => {
          console.log("请求失败：" + resp.status + "," + resp.statusText);
        });
    },
    //旅行团人员申报情况
    getTravelgroup() {
      var that = this;
      axios({
        method: "get",
        url: "" + apiUrl + "Travelgroup"
      })
      .then(function(resp) {
        that.getTravelgroupBar(resp.data);
      })
      .catch(resp => {
        console.log("请求失败：" + resp.status + "," + resp.statusText);
      });
    },
    //事项申报情况
    getEvents() {
      var that = this;
      axios({
        method: "get",
        url: "" + apiUrl + "Events"
      })
        .then(function(resp) {
          that.getEventsBar(resp.data);
        }).catch(resp => {
          console.log("请求失败：" + resp.status );
        });
    },
   //旅行社申报情况
    getTravelagency(){
      var that = this;
      axios({
        method: "get",
        url: "" + apiUrl + "Travelagency"
      })
        .then(function(resp) {
          that.getTravelagencyBar(resp.data);
        }).catch(resp => {
          console.log("请求失败：" + resp.status );
        });

    },
    //个人申报情况
    getPersonal:function(data){
       var that = this;
      axios({
        method: "get",
        url: "" + apiUrl + "Personal"
      })
        .then(function(resp) {
          that.getPersonalBar(resp.data);
        }).catch(resp => {
          console.log("请求失败：" + resp.status );
        });


    },
    //航空公司申报情况
    getAirport:function(data){
      var that = this;
      axios({
        method: "get",
        url: "" + apiUrl + "Airport"
      })
        .then(function(resp) {
          that.getAirportBar(resp.data);
        }).catch(resp => {
          console.log("请求失败：" + resp.status );
        });

    },

    chartssize(container, charts) {
      function getStyle(el, name) {
        if (window.getComputedStyle) {
          return window.getComputedStyle(el, null);
        } else {
          return el.currentStyle;
        }
      }
      var wi = getStyle(container, "width").width;
      var hi = getStyle(container, "height").height;
      charts.style.height = hi;
      // charts.style.width = wi
      //  charts.style.height = wi
    },
    startChart: function(chartPid, chartId, option) {
      let myChartId = document.getElementById(chartId);
      //高度计算

      this.chartssize(document.getElementById(chartPid), myChartId);

      let myChart = echarts.init(myChartId);

      // 为echarts对象加载数据
      myChart.clear();
      myChart.setOption(option, true);

      window.addEventListener("resize", function() {
        myChart.resize();
      });
    },
  
    // 以下各个模块数据处理
    getSystemBar: function(data) {
      this.colorCard = [
        {
          color: "#3499FF",
          icon: "el-icon-full-screen",
          current: "系统申报总数量",
          num: data.result.part1
        },
        {
          color: "#3CD6B1",
          icon: "el-icon-document-copy",
          current: "本月申报数量",
          num: data.result.part2
        },
        {
          color: "#FF7676",
          icon: "el-icon-monitor",
          current: "本周申报数量",
          num: data.result.part3
        },
        {
          color: "#FFBF5F",
          icon: "el-icon-date",
          current: "待审批数量",
          num: data.result.part4
        },
        {
          color: "#D9D9D9",
          icon: "el-icon-edit-outline",
          current: "已审批数量",
          num: data.result.part5
        }
      ];
    },
    getTravelgroupBar: function(data) {
      var list = data.result;
      var dateList = [];
      var travelgroupList1 = [];
      var travelgroupList2 = [];
      list.map((item, idx) => {
        dateList.push(item[0]);
        travelgroupList1.push(item[1]);
        travelgroupList2.push(item[2]);
      });
       console.log('travelgroupList1',travelgroupList1)
      var option = {
        color: ["#1890FF", "#FACC14", "#80FF80", "#FF8096", "#800080"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },

        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "value"
        },
        yAxis: {
          type: "category",
          data: dateList
        },
        series: [
          {
            name: "男",
            type: "bar",
            stack: "总量",
            label: {
              normal: {
                show: true,
                position: "insideRight"
              }
            },
            data: travelgroupList1
          },
          {
            name: "女",
            type: "bar",
            stack: "总量",
            label: {
              normal: {
                show: true,
                position: "insideRight"
              }
            },
            data: travelgroupList2
          }
        ]
      };

      this.startChart("parent", "chartmain", option);
    },
    getEventsBar: function(data) {
       
      var list = data.result;
      var dateList = [];
      var eventsList1 = [];
      var eventsList2 = [];
      var eventsList3 = [];
      var eventsList4 = [];
      var eventsList5 = [];
      var eventsList6 = [];
      var eventsList7 = [];
        
      list.map((item, idx) => {
        dateList.push(item[0]);
        eventsList1.push(item[1]);
        eventsList2.push(item[2]);
         eventsList3.push(item[3]);
          eventsList4.push(item[4]);
           eventsList5.push(item[5]);
            eventsList6.push(item[6]);
              eventsList7.push(item[7]);
      });
  
   
      var option = {
        tooltip: {
          trigger: "axis"
        },

        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: dateList
        },
        yAxis: {
          type: "value"
        },
          
        series: [
          {
            name: "“三单”申报",
            type: "line",
          
            data: eventsList1
          },
          {
            name: "遭返人员申报",
            type: "line",
          
            data: eventsList2
          },
          {
            name: "枪支弹药申报",
            type: "line",
          
            data: eventsList3
          },
          {
            name: "旅游团申报",
            type: "line",
           
            data: eventsList4
          },
          {
            name: "特殊情况申报",
            type: "line",
          
            data: eventsList5
          },
          {
            name: "特殊航班API申报",
            type: "line",
         
            data: eventsList6
          },
          {
            name: "24小时免签入境申报",
            type: "line",
            
            data: eventsList7
          }
        ]
      };
      this.startChart("parent", "infoLine", option);

     
    },
    getTravelagencyBar:function(data){

      var list = data.result;
      var dateList = [];
      var Travelagency = [];
      list.map((item, idx) => {
        dateList.push(item[0]);
        Travelagency.push(item[1]);
      });
       var infoBar_option = {
        color: ["#1890FF"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },

        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "value"
        },
        yAxis: {
          type: "category",
          data:dateList
        },
        series: [
          {
            name: "数量",
            type: "bar",
            stack: "总量",
            label: {
              normal: {
                show: true,
                position: "insideRight"
              }
            },
            data:Travelagency
          }
        ]
      };
        this.startChart("parent", "infoBar", infoBar_option);

    },
    getPersonalBar:function(data){
       var list = data.result;
       
      var dateList = [];
      var Personal = [];
      list.map((item, idx) => {
        dateList.push(item[0]);
        Personal.push(item[1]);
      });
      
      var per_declaration = {
        color: ["#3398DB"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "shadow" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data:dateList,
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: "value"
          }
        ],
        series: [
          {
            name: "数量",
            type: "bar",
            barWidth: "60%",
            data:Personal
          }
        ]
      };

    
      this.startChart("parent", "per_declaration", per_declaration);
      
    },
    getAirportBar: function(data) {
       var list = data.result;
       
      var dateList = [];
      
      
      list.map((item, idx) => {
        
         var obj= {value:  item[1], name:  item[0]}
        dateList.push( obj);
       
      });
      var pieOption = {
        tooltip: {
          trigger: "item",
          formatter: "{a} <br/>{b}: {c} ({d}%)"
        },

        series: [
          {
            name: "当前数据",
            type: "pie",
            radius: ["40%", "55%"],

            data:dateList
          }
        ]
      };
      this.startChart("parent", "pieOption", pieOption);
    
    }
  }
};
</script>
<style>
.ct {
  padding-top: 10px;
}
.weather {
  background: #cccccc;
}
.item {
  display: flex;
  flex-direction: row;
  align-items: center;
  background: #cccccc;
}
.flex_long {
  width: 70%;
  margin: 0px 10px 10px;
  border-radius: 7px;
}
.flex_sort {
  width: 30%;
  margin: 0px 10px 10px 0px;
  border-radius: 7px;
}

@media (max-width: 1370px) {
  .el-progress-circle {
    height: 100px !important;
    width: 100px !important;
  }
}
.long {
  height: 300px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
}
.sort {
  height: 300px;
  background: white;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
#icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 45px;
}
.title {
  text-align: left;
  background: white;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  padding: 10px 20px 0 20px;
  font-family: PingFangSC-Medium;
  font-size: 18px;
  color: #475669;
  letter-spacing: 0;
}
.long .card {
  position: relative;
  background: #fafafa;
  color: #13ce66;
  padding: 10px;
  margin: 10px;
}
</style>
