    makeFunc() {
      var args = Array.prototype.slice.call(arguments);
      var func = args.shift();
      return function() {
        return func.apply(
          null,
          args.concat(Array.prototype.slice.call(arguments))
        );
      };
    },

    unique(arr) {
      const res = new Map();

      return arr.filter(function(a) {
        console.log(a);
        //  res.set(a, 1)
        return !res.has(a) && res.set(a, 1);
      });
    },
    uniqueEs6(arr) {
