    deepCopy: function(obj, deep) {
      var o = obj instanceof Array ? [] : {};
      for (var k in obj) {
        var val = obj[k];
        console.log(k);

        if (deep && typeof val === "object") {
          o[k] = this.deepCopy(val, deep);
        } else {
          o[k] = val;
        }
      }
      return o;
    }
