<template>
  <main class="address_warp">
    <header>
      <el-button type="primary" @click="handleAddClick(type='新增')">增加</el-button>
      <el-button type="primary">查询</el-button>
    </header>
    <section>
      <el-table :data="tableData" border style="width: 80%">
        <el-table-column prop="address" label="省市区" min-width="20%" ></el-table-column>
        <el-table-column prop="subAddress" label="具体地址" min-width="20%"></el-table-column>
        <el-table-column fixed="right" label="操作" min-width="10%">
          <template slot-scope="scope">
            <el-button @click="handleUpadteClick(scope.row,type='修改',scope.$index)" type="text" size="small">修改</el-button>
            <el-button @click="onDelete(scope.$index)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </section>
    <el-dialog
      :title="currentType+'收货地址'"
      :visible.sync="pupVisible"
      width="50%"
      center>
      <ul>
        <li>
          <label>省市区:</label>
          <v-distpicker :province="temp.addressprovince" :city="temp.addresscity" :area="temp.addressdist"  @selected="sel"></v-distpicker>
        </li>
         <li>
          <label>具体地址：</label>
          <el-input v-model="subAddress" placeholder="请输入具体地址"></el-input>
        </li>
      </ul>
      <span slot="footer" class="dialog-footer">
        <el-button @click="pupVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSave(currentType)">确 定</el-button>
      </span>
    </el-dialog>
  </main>
</template>


<script>
import VDistpicker from 'v-distpicker'
export default {
  name:'address',
  components: { VDistpicker },
  methods: {
    handleUpadteClick(row,type,index) {
      this.pupVisible = true
      this.currentType = type
      let copyParam = JSON.parse(JSON.stringify(row))
      let adLists = copyParam.address.split('/')
      this.temp.addressprovince = adLists[0]
      this.temp.addresscity = adLists[1]
      this.temp.addressdist = adLists[2]
      this.subAddress = copyParam.subAddress
      this.address = copyParam.address
      this.idx = index
    },
    handleAddClick(type){
      this.pupVisible = true
      this.currentType = type
    },
    sel(data) {
      this.temp.addressprovince = data.province.value
      this.temp.addresscity = data.city.value
      this.temp.addressdist = data.area.value
      this.address = this.temp.addressprovince +'/'+ this.temp.addresscity +'/'+ this.temp.addressdist
      console.log(this.citydata);
    },
    onSave(currentType){
      var params = {}
      params.address = this.address
      params.subAddress = this.subAddress
      if(currentType === '新增'){
        this.tableData.push(params)
      }else if (currentType === '修改'){
        console.log(params)
        this.tableData.splice(this.idx,1,params)
      }
      this.pupVisible = false
      this.clearData()
    },
    onDelete(idx){
      this.tableData.splice(idx,1)
    },
    clearData () {
      this.address = ''
      this.subAddress = ''
      this.temp.addressprovince = ''
      this.temp.addresscity = ''
      this.temp.addressdist = ''
      this.idx = ''
    }
  },

  data() {
    return {
      idx:'',
      temp: {
        addressprovince: '',
        addresscity: '',
        addressdist: '',
      },
      pupVisible: false,
      currentType: '',
      address:'',
      subAddress:'',
      tableData: [
        {
          address: "天津市/天津城区/宝坻区",
          subAddress:'上海市普陀区金沙江路',
          zip: 11122
        },
        {
          address: "山西省/长治市/郊区",
          subAddress:'上海市普陀区金沙江路',
          zip: 200333
        }
      ]
    };
  }
};
</script>
<style lang="less" scope>
.address_warp{
  ul{
     list-style:none;
     width: 80%;
    li{
      margin: 5px;
      label{
        width: 100px;
        line-height: 3;
      }

    }
  }
  width: 100%;
  height: 100%;
  border: 1px solid #cccccc;
  padding: 20px;
  header{
    display: flex;
    padding: 10px 10px 10px 0;
  }
}
</style>