<template>

  <div class="content">
       <span class='sysName'>Vue-Hjb-Admin</span> 
       <marquee   style="width: 400px; "  onMouseOut="this.start()" onMouseOver="this.stop()">onMouseOut="this.start()" ：用来设置鼠标移出该区域时继续滚动 onMouseOver="this.stop()"：用来设置鼠标移入该区域时停止滚动</marquee>

      <span class="userState"> 
          <!-- <img class="img_hed" src="../common/images/sdlogo.png" alt="logo"> -->
          <div style="display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;">
             <p class='username' style=" font-size: 22px;">{{account}}</p>
             <p class="marginLeft"   @click="changePwd()">修改密码</p>
             <p class="marginLeft"  @click="loginOut()">退出</p>
             <!-- <p class='username'>张三</p> -->
          </div>
      </span>
     
  
        <!-- 操作弹框 -->
    <el-dialog title="修改密码"
               :modal-append-to-body="false"
               :visible.sync="pwdDialog.visible"
               :close-on-click-modal="false"
               width="500px"
               :lock-scroll="true"
               :showClose= "false" >
      <el-form
          :model="pwdDialog.data"
          :rules="rules"
          ref="pwdForm"
          label-width="100px">

            <el-row>
              <el-col :span="24">
                <el-form-item label="旧密码" prop="oldLoginPwd">
                  <el-input type="password" v-model="pwdDialog.data.oldLoginPwd"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24">
                <el-form-item label="新密码" prop="newLoginPwd">
                  <el-input type="password" v-model="pwdDialog.data.newLoginPwd">
                  </el-input>
                </el-form-item></el-col>
            </el-row>
            <el-row>
            <el-col :span="24">
                <el-form-item label="确认密码" prop="confirmLoginPwd">
                  <el-input type="password" v-model="pwdDialog.data.confirmLoginPwd">
                  </el-input>
                </el-form-item></el-col>
            </el-row>

        </el-form>

      <span slot="footer" class="dialog-footer">
          <el-button @click="cancel()">取 消</el-button>
          <el-button type="primary" @click="submitForm()">确 定</el-button>
        </span>
    </el-dialog>

</div>
</template>

<script>
export default {
    name: 'heade',
    data () {
      var validateOldLoginPwd = (rule,value,callback) => {
        if (checkNull(value)) {
          callback(new Error('旧密码不能为空'));
        } else {
          var reg =  /^[a-zA-Z0-9_]{6,12}$/;
          var result = value.match(reg);
          if (result == null) {
            callback(new Error('密码只能含字母、数字、下划线，长度6-12个字符'))
          } else {
            callback();
          }
        }
      };
      var validateNewLoginPwd= (rule, value,callback) => {
        if (checkNull(value)) {
          callback(new Error('新密码不能为空'));
        } else {
          var reg = /^[a-zA-Z0-9_]{6,12}$/;
          var result = value.match(reg);
          if (result == null) {
            callback(new Error('密码只能含字母、数字、下划线，长度6-12个字符'));
          } else {
            callback();
          }
        }
      };
       var validateConfirmLoginPwd= (rule, value,callback) => {
        if (checkNull(value)) {
          callback(new Error('确认密码不能为空'));
        } else {
          var reg = /^[a-zA-Z0-9_]{6,12}$/;
          var result = value.match(reg);
          if (result == null) {
            callback(new Error('密码只能含字母、数字、下划线，长度6-12个字符'));
          } else {
            callback();
          }
        }
      };
      function checkNull(value) {
        if (value === '' || !value) {
          return true;
        } else {
          return false;
        }
      }
      return {
        msg: 'Welcome to Your Vue.js App',
        date:'',
        pwdDialog:{
          visible:false,
          data:{
            oldLoginPwd:'',
            newLoginPwd:'',
            confirmLoginPwd:'',
          },

        },
        account:"",
        currentUser:{
          userName:null,
        },
        rules: {
             oldLoginPwd: [{required: true, validator:validateOldLoginPwd, trigger: 'blur'}],
              newLoginPwd: [{required: true, validator:validateNewLoginPwd, trigger: 'blur'}],
             confirmLoginPwd: [{required: true, validator: validateConfirmLoginPwd, trigger: 'blur'}]
        },

      }
    },
    props: ['msgVal'],
    mounted(){
       var loginInfo = JSON.parse(localStorage.getItem('loginForm'));
       if(loginInfo){
         this.account = loginInfo.account
       }
      
      
      // this.getsTime()
    },
    created(){
      // this.getCurrentUser();
    },
    methods: {
      getCurrentUser(){
      
      },
      callback(){
       

      },
      getsTime(){
              var d = new Date();
              var str = d.getFullYear()+"年"+(d.getMonth()+1)+"月"+d.getDate()+"日";
              this.date= str;
        },
        loginOut(){
        
           this.$router.push({path:'/'});
        },

      //修改密码弹框
      changePwd(){
        this.pwdDialog.data = {};
        this.pwdDialog.visible = true;
      },
      //取消弹框
      cancel(){
        this.$refs['pwdForm'].clearValidate();
        this.pwdDialog.data = {};
        this.pwdDialog.visible = false;
      },
      //修改密码
      submitForm(){
        var _this = this;
         let validFlg = true;
         _this.$refs['pwdForm'].validate((valid) => {
           validFlg = valid;
         });
         if (!validFlg) return;
          if (_this.pwdDialog.data.newLoginPwd != _this.pwdDialog.data.confirmLoginPwd ) {
              this.$message({ type: 'warning', message: '两次输入的密码不一致！' });
              return;
          }
          if (_this.pwdDialog.data.newLoginPwd == _this.pwdDialog.data.oldLoginPwd) {
              this.$message({ type: 'warning', message: '新密码和旧密码不能相同！' });
              return;
          }

      
          var dataNow =  this.pwdDialog.data;
          var loginForm = JSON.parse(localStorage.getItem('loginForm'));
          var obj={};
          obj.account = loginForm.account;
          obj.oldpassword = dataNow.oldLoginPwd; //yuan
          obj.newpassword = dataNow.newLoginPwd; //xin
   
       
      }
    },

}
</script>
<style scoped lang="less">
  .content{
        background-color: #23262E;
    color: white;

   
    box-shadow: 0 4px 16px 0 rgba(0,0,0,0.12);
    height: 8%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    .sysName{
      padding-left: 5px;
      font-weight: 600;
      font-size: 30px;
      letter-spacing: 2px;
      line-height: 2.3;
          min-width: 200px;
    }
    .userState{
      display: flex; 
         min-width: 200px;
      right: 30px;
    }
  }
  .marginLeft{
        font-size: 17px;
      margin-left: 10px;
          cursor: pointer;
  }
  .img_hed{
       
        
        width: 45px;
        height: 45px;
        margin: 0 10px;
        position: relative;
        display: inline-block;
        top: 13px;
  }
  .left {

    flex: 0 1 175px;
    text-align: center;
    line-height: 2.8;
    ont-family: PingFangSC-Medium;
    font-size: 28px;
    color: #4971CE;
    letter-spacing: -0.68px;
    margin-left: 12px;

  }
  .center {
    flex-grow: 1;
    text-align: center;
    line-height: 3.4;
    font-family: PingFangSC-Regular;
    font-size: 22px;
    color: rgba(0,0,0,0.50);
    letter-spacing: -0.83px;
    text-align: center;
    padding-left: 100px;
  }
  .right {
      flex: 0 1 300px;
        text-align: right;
        line-height: 2.1;
       .username{

          font-family: PingFangSC-Regular;
          font-size: 22px;
          color: #4971CE;
          letter-spacing: -0.68px;
          text-align: center;
      }
      i{
        color: #4971CE;
        font-size: 30px;

      }
  }


</style>

